<template>
        <li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120"  :alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{ item.product.title }}
              </h3>
              <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

              <CartVolume @increment="increment" @decrement="decrement" :amount="amount"/>

              <b class="product__price">
                {{ item.amount * item.product.price | numberFormat }} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>
    import numberFormat from '@/helpers/numberFormat';
    import { mapMutations } from 'vuex';
    import CartVolume from '@/components/CartVolume.vue';
    import { mapActions } from 'vuex';


    export default {
        filters: { numberFormat },
        props: ['item'],
        components: { CartVolume },
        computed: {
            amount: {
                get() {
                    return this.item.amount;
                },
                set(value) {
                    this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value})
                }
            }
        },
        methods: {
          /* ...mapMutations({ deleteProduct: 'deleteCartProduct' }), */
          ...mapActions({deleteProduct: 'deleteProductToCart', updateCartProductAmount: 'updateCartProductAmount'}),

          decrement(){
            if (this.item.amount > 1) {
              this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: Math.max(1, this.item.amount - 1)})
            }
          },
          increment() {
            this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: this.item.amount + 1})
          },
        },
        
    }


</script>