<template>
    <router-link class="header__cart" :to="{name: 'cart'}" aria-label="Корзина с товарами">
        <svg width="30" height="21" fill="currentColor">
            <use xlink:href="#icon-cart"></use>
        </svg>
        <span class="header__count" aria-label="Количество товаров">{{ amountTotal }}</span>
    </router-link>  
</template>

<script>
    import { mapGetters, mapState } from 'vuex';

    export default {
        computed: {
            ...mapGetters({amountTotal: 'cartTotalQuantity'}),
            ...mapState({productsLoading: 'productsLoading'})
        },
    }
</script>