<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"></ProductFilter>

      <section class="catalog">
        <div v-if="productsLoading" class="spinner"><img src="@/img/Fidget-spinner.gif"></div>
        <div v-else-if="productsLoadingFailed">Произошла ошибка при загрузке товаров<button @click.prevent="loadProducts">Попробовать еще раз</button></div>
        <ProductList v-else :products="products" ></ProductList>
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" ></BasePagination>
        
      </section>
    </div>
  </main>
</template>

<script>
  import ProductList from '@/components/ProductList.vue';
  import BasePagination from '@/components/BasePagination.vue';
  import ProductFilter from '@/components/ProductFilter.vue';
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';

  export default {
    components: {ProductList, BasePagination, ProductFilter},
    data() {
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColorId: 0,
        page: 1,
        productsPerPage: 3,
        productsData: null,
        productsLoading: true,
        productsLoadingFailed: false

      }
    },
    computed: {
      products(){
        return this.productsData
        ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          }
        }) 
        : {};
      },
      countProducts(){
        return this.productsData ? this.productsData.pagination.total : 0;
      }
    },
    methods: {
      loadProducts() {
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductsTimer);
        this.loadProductsTimer = setTimeout(() => {
          axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              colorId: this.filterColorId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
        }, 1000);
      }
    },
    watch: {
      page() {
        this.loadProducts();
      },
      filterPriceFrom() {
        this.loadProducts();
      },
      filterPriceTo() {
        this.loadProducts();
      },
      filterCategoryId() {
        this.loadProducts();
      },
      filterColorId() {
        this.loadProducts();
      },
    },
    created() {
      this.loadProducts();
    }
  }
</script>

<style>
  .spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    min-height: 500px;
    background-color: rgba(255, 255, 255, 1);
    z-index: 1000;
  }

</style>