<template>
              <div class="product__counter form__counter">
                <button class="volume-btn" aria-label="Убрать один товар" @click.prevent="decrementItem">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text"  v-model.number="amount" name="count">

                <button class="volume-btn" aria-label="Добавить один товар" @click.prevent="incrementItem">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

</template>

<script>

    export default {
      props: [ 'amount' ],
      methods: {
        decrementItem() {
          this.$emit('decrement');
        },
        incrementItem() {
          this.$emit('increment');
        }
      },
    }
</script>

<style>
  .volume-btn {
    cursor: pointer;
  }
</style>