import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { setTimeout } from 'core-js';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartProductsData: [],
        orderInfo: null,

        productsLoading: false,
        productsLoadingFailed: false,


    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];

        },
        updateCartProductAmount(state, {productId, amount}) {
            const item = state.cartProducts.find(item => item.productId === productId);

            if(item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            })
        },
        resetProductCart(state, payload) {
            state.cartProducts = payload
        },
        syncOrderInfo(state, payload) {
            state.orderInfo = payload
        },
    },
    getters: {
        cartDetailsPoduct(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                }
            });
        },
        cartTotalPrice(state, getters) {
            return getters.cartDetailsPoduct.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        },
        cartTotalQuantity(state) {
            return state.cartProducts.length
        },
        resOrderInfo(state) {
            let amountAll = state.orderInfo.basket.items.reduce((acc, item) => acc + item.quantity, 0)
            return Object.assign({}, state.orderInfo, {amount: amountAll})
          },
    },
    actions: {
        loadOrderInfo(context, orderId) {
            return axios
                .get(API_BASE_URL + '/api/orders/' + orderId, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateOrderInfo', response.data);
                });
        },
        loadCart(context) {
            context.state.productsLoading = true
            context.state.productsLoadingFailed = false

            return axios
                .get(API_BASE_URL + '/api/baskets', {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    if(!context.state.userAccessKey) {
                        localStorage.setItem('userAccessKey', response.data.user.accessKey);
                        context.commit('updateUserAccessKey', response.data.user.accessKey);
                    }
                    context.commit('updateCartProductsData', response.data.items);
                    context.commit('syncCartProducts');
                })
                .then(() => context.state.productsLoading = false)
                
        },
        addProductToCart(context, {productId, amount}) {
            context.state.productsLoading = true
            context.state.productsLoadingFailed = false

            return (new Promise(resolve => setTimeout(resolve, 2000)))
                .then(() => {
                    return axios
                        .post(API_BASE_URL + '/api/baskets/products', {
                            productId: productId,
                            quantity: amount
                        }, {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            context.commit('updateCartProductsData', response.data.items);
                            context.commit('updateCartProductAmount');
                        })
                        .catch(err => {
                            context.state.productsLoadingFailed = false
                            context.commit('syncCartProducts');
                        })
                        .then(() => context.state.productsLoading = false)

                })

        },
        updateCartProductAmount(context, {productId, amount}) {
            context.commit('updateCartProductAmount', {productId, amount});

            if(amount < 1) {
                return;
            }

            return axios
                .put(API_BASE_URL + '/api/baskets/products', {
                    productId: productId,
                    quantity: amount
                }, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items);
                })
                .catch(() => {
                    context.commit('syncCartProducts');
                })
        },
        deleteProductToCart({commit, state}, productId) {
            commit('deleteCartProduct', productId)
            return axios
                .delete(API_BASE_URL + '/api/baskets/products', {
                    params: {
                        userAccessKey: state.userAccessKey,
                    },
                    data: {
                        productId
                    }
                })
                .then(response => {
                    commit('updateCartProductsData', response.data.items)
                    commit('resetProductCart')
                    
                })
                .catch(err => {
                    state.productsLoadingFailed = false,
                    commit('syncCartProducts'),
                    console.log(productId)
                })
                .then(() => state.productsLoading = false)
        },
        addOrderInfo({commit}, data) {
            commit('syncOrderInfo', data)
        },
        
    }
});