<template>
  <main class="content container" v-if="productsLoading">
    <div class="spinner">
      <img src="@/img/Fidget-spinner.gif">
    </div>
  </main>
  <main v-else-if="productsLoadingFailed" class="cart">Произошла ошибка при загрузке товаров!</main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ totalQuantity }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item"></CartItem>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';
  import { mapGetters, mapState } from 'vuex';
  import CartItem from '@/components/CartItem.vue';
  import axios from 'axios';
    export default {
        data() {
          return {
            productsLoading: true,
            productsLoadingFailed: false
          }
        },
        filters: { numberFormat },
        components: { CartItem },
        computed: {
          console: () => console,
          ...mapGetters({products: 'cartDetailsPoduct', totalPrice: 'cartTotalPrice', totalQuantity: 'cartTotalQuantity'}),
        },
        methods: {
          loadCart() {
            clearTimeout(this.loadCartTimer);
            this.loadCartTimer = setTimeout(() => {
              this.productsLoading = false;
            }, 2000);
          }
        },
        created() {
          this.loadCart();
        }
    }
</script>
